html {
    font-size: 62.5%;
    line-height: 1.6rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-sizing: border-box; 
}
  
* {
    margin: 0;
    padding: 0;
    box-sizing: inherit; 
}
.grid {
width: 100%;
display: block;
padding: 0;
}

.grid.wide {
max-width: 1200px;
margin: 0 auto;
}

.row {
display: flex;
flex-wrap: wrap;
margin-left: -4px;
margin-right: -4px;
}

.row.no-gutters {
margin-left: 0;
margin-right: 0;
}

.col {
padding-left: 4px;
padding-right: 4px;
}

.row.no-gutters .col {
padding-left: 0;
padding-right: 0;
}

.c-0 {
display: none;
}

.c-1 {
flex: 0 0 8.33333%;
max-width: 8.33333%;
}

.c-2 {
flex: 0 0 16.66667%;
max-width: 16.66667%;
}

.c-3 {
flex: 0 0 25%;
max-width: 25%;
}

.c-4 {
flex: 0 0 33.33333%;
max-width: 33.33333%;
}

.c-5 {
