max-width: 41.66667%;
}

.c-6 {
flex: 0 0 50%;
max-width: 50%;
}

.c-7 {
flex: 0 0 58.33333%;
max-width: 58.33333%;
}

.c-8 {
flex: 0 0 66.66667%;
max-width: 66.66667%;
}

.c-9 {
flex: 0 0 75%;
max-width: 75%;
}

.c-10 {
flex: 0 0 83.33333%;
max-width: 83.33333%;
}

.c-11 {
flex: 0 0 91.66667%;
max-width: 91.66667%;
}

.c-12 {
flex: 0 0 100%;
max-width: 100%;
}

.c-o-1 {
margin-left: 8.33333%;
}

.c-o-2 {
