/* CSS MODAL MESSAGE */

.modal_message {
  position: fixed;
  top: 50%;
  left: 50%;
  display: none;
  align-items: center;
  justify-content: center;
  padding: 20px;
  min-width: 250px;
  min-height: 150px;
  background: rgba(0,0,0,0.5);
  transform: translate3d(-50%, -50%, 0);
  opacity: 1;
  transition: opacity 1.5s ease;
  color: #FFF;
  font-size: 18px;
  border-radius: 8px;
  box-sizing: border-box;
  z-index: 1000;
}

.modal_message.active{
  opacity: 1;
  display: flex;
}

.modal_message.feling{
  display: block;
  display: flex;
  opacity: 0;
  transition: 0.4s;
}
